@font-face {
  font-family: 'TTLakes-Bold.ttf';
  src: url(./fonts/TTLakes-Bold.ttf);
  Font-style: Bold;
  Font-size: 28px;
  color: #1B1C21; }

@font-face {
  font-family: 'TTLakesCondensed-Medium.ttf';
  src: url(./fonts/TTLakes-Medium.ttf); }

@font-face {
  font-family: 'TTLakesCondensed-Regular.ttf';
